import { Injectable } from '@angular/core';
import { CurrencyRegistryResponse, LimitsHttpService, CurrencyGetResponseBody } from '@backbase/data-ang/limits';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class CurrencyService {
  constructor(private readonly limitService: LimitsHttpService) {}

  currencyInfo(): Observable<CurrencyRegistryResponse | undefined> {
    return this.limitService.getCurrency().pipe(
      map((res: CurrencyGetResponseBody) => {
        return res ? res['currency-registry'] : {};
      }),
    );
  }
}
